<script>
import axios from "axios"

export default {
  setup() {
    axios.interceptors.request.use(
        function (config) {
          // 在发送请求之前做些什么
          let token = localStorage.getItem('token');
          if (token) {
            config.headers.Authorization = token;
          }
          return config;
        },
        function (error) {
          // 对请求错误做些什么
          return Promise.reject(error);
        }
    );
  }
}
</script>
